body {
    margin: 0;
    padding: 0;
    font-family: sans-serif;
    font-size: 16px;
}

/** Main Layout rules */
.App {
    min-height: 100vh;
}

.App {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex: 1 1;
        flex: 1 1;
}

.App .body {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex: 1 1;
        flex: 1 1;
}

.body .sidebar {
    -ms-flex-order: -1;
        order: -1;
}

.body .content {
    -ms-flex: 1 1;
        flex: 1 1;
}

.body .sidebar {
    max-width: 12em;
    -ms-flex: 0 0 12em;
        flex: 0 0 12em;
}

.App .header {
    height: 4em;
}

.App .footer {
    height: 4em;
}

/** Text */
.text-red {
    color: red;
}


